.cart-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 2px solid #8a2b06;
  padding: 1rem 0;
  margin: 1rem 0;
}

.qtInfo {
  width: 35%;
}

.cart-item h2 {
  margin: 0 0 0.5rem 0;
  color: #363636;
}

.summary {
  width: 45%;
  max-width: 15rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media only screen and (max-width: 250px) {
  .summary {
    width: 50%;
  }
}

.price {
  font-weight: bold;
  color: #8a2b06;
}

.amount {
  font-weight: bold;
  border: 1px solid #ccc;
  padding: 0.25rem 0.75rem;
  border-radius: 6px;
  color: #363636;
}

.actions {
  display: flex;
  flex-direction: column;
}

.cart-item button {
  font: inherit;
  font-weight: bold;
  font-size: 2rem;
  color: #8a2b06;
  border: 1px solid #8a2b06;
  width: 3rem;
  text-align: center;
  border-radius: 6px;
  background-color: transparent;
  cursor: pointer;
  margin-left: 1rem;
  margin: 0.25rem;
}

.cart-item button:hover,
.cart-item button:active {
  background-color: #8a2b06;
  color: white;
}

@media only screen and (max-width: 318px) {
  .qtInfo {
    width: 60%;
  }
  .actions {
    flex-direction: row;
    margin-right: 1rem;
  }
}

@media only screen and (min-width: 319px) {
  .qtInfo {
    width: 60%;
  }
  .actions {
    flex-direction: row;
    margin-right: 1rem;
  }
}

@media only screen and (min-width: 416px) {
  .summary {
    width: 38.5%;
  }
}
